import React, { useEffect } from "react";
import "./App.css";
import "leaflet/dist/leaflet.css";
import Map from "./Map";
import tiler from "./tiler";
import * as FaaService from "./services/FaaService";
import { Sectionals } from "./services/Sectionals";

function App() {
  const selectedSectional = Sectionals.SCHI;

  async function extract() {
    const tiffBlob = await FaaService.getSectionalAsTiff(selectedSectional);

    tiler.postMessage({ file: tiffBlob });
  }

  useEffect(() => {
    extract();
  }, []);

  return (
    <div className="App">
      <header className="App-header">
        <Map sectional={selectedSectional} />
      </header>
    </div>
  );
}

export default App;
